<template>
  <el-upload
    class="img-upload"
    ref="upload"
    action="http://localhost:8443/api/admin/content/banks/covers"
    with-credentials
    :on-preview="handlePreview"
    :on-remove="handleRemove"
    :before-remove="beforeRemove"
    :on-success="handleSuccess"
    multiple
    :limit="1"
    :on-exceed="handleExceed"
    :file-list="fileList">
    <el-button size="small" type="primary"></el-button>
    <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
  </el-upload>
</template>

<script>
export default {
  name: 'ImgUpload',
  data () {
    return {
      fileList: [],
      url: ''
    }
  },
  methods: {
    handleRemove (file, fileList) {
      console.log(file, fileList)
    },
    handlePreview (file) {
      console.log(file)
      //  file 
      // console.log(file.response)
    },
    handleExceed (files, fileList) {
      this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `)
    },
    beforeRemove (file, fileList) {
      return this.$confirm(` ${file.name}`)
    },
    handleSuccess (response) {
      this.url = response
      // console.log(this.url)
      this.$emit('onUpload')
      this.$message.warning('')
    },
    clear () {
      this.$refs.upload.clearFiles()
    }
  }
}
</script>

<style scoped>
  .img-upload {
    /*height: 200px;*/
  }
</style>



